@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Be+Vietnam+Pro:wght@300;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#hidden {
  display: none;
}

.game-container {
  display: grid;
  grid-template-columns: 650px;
  justify-content: center;
  align-items: center;
}
/* font-family: 'Abril Fatface', cursive;
font-family: 'Be Vietnam Pro', sans-serif; */

:root {
  --beige: #F0E7D8;
  --khaki: #BFAE9C;
  --darkmoss: #5d6857;
  --moss: #707B6C;
  --chocolate: #a0614a;
}

* {
  margin:0;
  padding:0;
}

.App {
  text-align: center;
  margin: 0 auto;
  text-align: -webkit-center;
}

body {
  background: linear-gradient(to top left, #F0E7D8, rgb(255 244 230)) fixed;
  background: linear-gradient(to top left, var(--beige), rgb(255 244 230)) fixed;
  margin: 0 auto;
  font-family: 'Be Vietnam Pro', sans-serif;
  text-align: center;
}
p {
  margin: 0;
  color: rgb(61 61 61);
  font-size: 1.2em;
}
